<template>
    <div id="normTemplate">
        <el-tag
            :key="tag"
            v-for="tag in dynamicTags"
            :closable="true"
            :close-transition="false"
            @close="handleClose(tag)"
            type="primary"
        >
            {{tag}}
        </el-tag>
        <el-input
            v-if="inputVisible"
            v-model.trim="inputValue"
            ref="saveTagInput"
            size="mini"
            @keyup.enter.native="handleInputConfirm"
            @blur="handleInputConfirm"
            class="tag_input"
            :maxlength="6"
        >
        </el-input>
        <el-button v-else size="small" @click="showInput"><i class="el-icon-plus"></i> 添加值</el-button>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                dynamicTags: this.normValueList,
                inputVisible: false,
                inputValue: ''
            }
        },
        props: ['normValueList', 'normName'],  //normName为规格的名字 如'颜色'  通过名字判断限制， 但存在问题，倘若'颜色'名字改成'颜色啊'呢
        methods: {
            handleClose(tag) {
                this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
            },
            showInput() {
                if(this.normName == '颜色') {
                    if(this.normValueList.length > 9) {
                        this.$message({
                            message: '该规格值最多只能添加10个',
                            duration: 1000,
                            type: 'info'
                        });
                        return
                    }
                }
                else {
                    if(this.normValueList.length > 4) {
                        this.$message({
                            message: '该规格值最多只能添加5个',
                            duration: 1000,
                            type: 'info'
                        });
                        return
                    }
                }
                this.inputVisible = true;
                this.$nextTick(_ => {
                    this.$refs.saveTagInput.$refs.input.focus();
                });
            },
            handleInputConfirm() {
                let inputValue = this.inputValue;
                for(let i=0, len=this.dynamicTags.length; i<len; i++) {
                    if(inputValue == this.dynamicTags[i]) {
                        this.$message({
                            message: '规格值重名,请重新填写。',
                            duration: 1000,
                            type: 'warning'
                        });
                        this.inputVisible = false;
                        this.inputValue = '';
                        return
                    }
                }
                if (inputValue) {
                    this.dynamicTags.push(inputValue);
                }
                this.inputVisible = false;
                this.inputValue = '';
            }
        },
        watch: {
            normValueList(newValue, oldValue) {
                this.dynamicTags = newValue
            }
        }
    }
</script>
<style lang="scss" scoped>
    #normTemplate {
        display: inline-block;
        &>span {
            margin-left: 5px;
        }
        .el-button {
            margin-left: 7px;
        }
        .tag_input {
            width: 70px;
        }
        .el-input.tag_input  {
            width: 70px;
            margin-left: 7px;
        }
    }
</style>



